.App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

  h2{
    font-family:Arial, Helvetica, sans-serif;
  }

  li{
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    font-style: italic;
  }

  #div3{
    display: flex;
    justify-content: center;
}

#btn{
	padding:10px;
	width:250px;
	cursor: pointer;
	color:#1976d2;
	background-color:yellow;
	border: none;
	border-radius:25px;
	font-family:sans-serif;
	font-size: 30px;
  margin: 10px;
}
#btn:hover{ 
	background: #03e9f4; 
	color: #fff; 
	border-radius: 5px; 
	box-shadow: 0 0 5px #03e9f4, 
				0 0 25px #03e9f4, 
				0 0 50px #03e9f4, 
				0 0 100px #03e9f4; 
} 