#mainPrimePlaylist {
    display: flex;
    flex-direction: row;
}

#s1 {
    display: flex;
    margin:5px;
}

#p {
    height:270px;
    width: 300px;
    border: 2px solid gray;
    margin: 10px;
    text-align: center;
    border-radius: 10px;
}

#p:hover {
    color: blue;
    opacity: .5;
    background-color: aqua;
    cursor: pointer;
    height: 330px;
    width: 300px;
}

#mainPrimePlaylist {
    -ms-overflow-style: none;
    scrollbar-width: none;
    overflow-y: scroll;
    scroll-behavior: smooth;
}

#mainPrimePlaylist::-webkit-scrollbar {
    display: none;
}

::-webkit-scrollbar {
    display: none;
}

.cntrls{
    margin-right:30px;
}

.SoNg-coNtaiNer:hover .cntrls {
    display: block;
}

.cntrls {
    display: none;
}


.high{
    background-color:oldlace;
}

.p:hover .cntrls {
    display: block;
}

.tpc:hover{
    background-color:black;
    border-radius:15px;
    color:yellow;
    opacity: .8;
}

.item1:hover, .slide:hover{
    transform: scale(1.1);
}