.soDiv {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  text-align: center;
  margin: 10px;
  height:100px;
  width: 300px;
  border: none;
  background-color:#f0eedf;
  border-radius: 16px;
  padding: 24px;
}

#soDiv:hover p {
  opacity: 0.5;
  cursor: pointer;
}

#imgDiv {
  border-radius: 20px;
}

#imgDiv:hover {
  cursor: pointer;
}

#soDiv:hover h3 {
  opacity: 0.5;
  cursor: pointer;
}

#album {
  margin: 10px;
}

#contD {
  display: flex;
  justify-content: center;
}

.contrls {
  justify-content: center;
  margin-top: 20px;
}

.ConTainer1 {
  display: flex;
  width: 100%;
  overflow: auto;
  padding:15px;
}

.left-div {
  flex: 0 0 70%;
  background-color:rgba(255, 223, 238, .4);
  border-radius:10px;
  overflow: auto;
}

.right-Div{
  flex: 0 0 29%;
  overflow: auto;
  background-color:#dcd0ff;
  height:500px;
  border-radius:10px;
  scrollbar-width: none;
}

.right-div::-webkit-scrollbar {
  display: none;
}


.MainAudioPlr {
  display: flex;
  justify-content: center;
}

.audioPlayer {
  text-align: center;
}

#albImg {
  border-radius: 20px;
}

.prev,
.play,
.nex {
  margin-left: 30px;
  padding: 10px;
  color: #15124b;
  opacity: .6;
  border: none;
  cursor: pointer;
  border-radius: 10px;
}

.prev:hover,
.play:hover,
.nex:hover {
  color: #15124b;
  opacity: 1
}



#progressBar:hover,
#progressBar {
  opacity: .8
}
