.profile-card {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  padding: 20px;
  transition: transform 0.3s ease;
  margin: 20px;
  height:auto;
}
.profile-img{
  height:350px;
}

.profile-image {
  margin-right: 20px;
  border-radius: 70%;
  width: 150px;
  height: 150px;
}

.profile-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

input[type="file"] {
  display:flex;
  flex-direction: row;
}


.profile-image:hover img {
  transform: scale(1.1);
}

.profile-details {
  flex: 1;
  margin-left: 50px;
  height:350px;
}

.profile-details h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.profile-details p {
  font-size: 1rem;
  margin-bottom: 5px;
}

#profileSub{
  border: none;
  background-color: orange;
  width:150px;
  height:30px;
  text-align: center;
  color:black;
  font-family:Verdana, Geneva, Tahoma, sans-serif;
  border-radius: 10px;
  cursor: pointer;
}

#profileSub:hover {
  transform: scale(1.1);
  background-color:#10de10;
}

