.playList-Container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}


#displaySongsDiv{
  width:auto;
  height:435px;
  padding:10px;
  margin:20px;
  border:2px solid green;
  border-radius: 20px;
  background-color:#dbdfe5;
}


#SongsDiv{
  width:300px;
  height:auto;
  margin:10px;
  padding:10px;
  border: 2px solid aqua;
  border-radius: 10px;
  background-image: url("../Pages/img2.jpg");
  background-repeat:repeat;
  background-position:center;
  background-size: cover;
}

#displaySongsDiv{
-ms-overflow-style:none;
scrollbar-width:none;
overflow-y:scroll;
scroll-behavior: smooth;
}
#dispSongsDiv::-webkit-scrollbar{
display:none;
} 
