.card{
  display: flex;
  justify-content: center;
  text-align:center;
  background-color: #dbdfe5;
  margin:2px;
  border-radius: 5px;
  height:60px;
  overflow: auto;
}

.searchCard{
  text-align:center;
  margin:3px;
  border-radius:10px;
  background-color: #dbdfe5;
  height:170px;
  overflow:scroll;
  position:relative;
}
.card-group {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  margin: 10px;
}

.search-box {
  padding: 10px;
  height: auto;
  width: 300px;
  position:sticky;
  border: 2px solid blueviolet;
  border-radius: 20px;
  margin-top: 33px;
}

.searchCard, .serchCar{
  background-image: linear-gradient(to bottom,
      rgb(255 255 0 / 50%),
      rgb(0 0 255 / 50%)), url("../Pages/music.jpg");
  background-repeat: no-repeat;
  background-attachment: inherit;
  background-position: center;
  background-size: cover;
  
}

.serchCar{
display: flex;
justify-content: center;
align-items: center;
}

#abcdefg{
justify-content: center;
align-items: center;
}

.car{
  display: flex;
  justify-content: center;
  text-align:center;
  background-color: #dbdfe5;
  margin:2px;
  border-radius: 5px;
  height:60px;
  width:300px;
}