#addSongNav{
	display:flex;
	flex-direction:row;
	flex-wrap:wrap;
	justify-content:flex-start;
    background-color: black;
}
#addSongDiv, #addSongNav{
    background-image: url('../Pages/Img1.jpg');
    background-repeat:no-repeat;
    background-attachment:fixed;
    background-position:center;
    background-size: cover;
    width: 100%;
    height:auto;
}
h1{
	text-align: center;
	color:yellow;
	font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.songn, .songg, .songa, .songl{
	color:#03e9f4;
    font-family:'Courier New', Courier, monospace;
}
#addSongField{
	width: 50%;
	font-family: sans-serif;
    border:1px solid #dbdfe5;
    border-radius: 10px;
    padding: 30px;
}

#addSong1{
	display:flex;
	flex-direction:row;
	flex-wrap:wrap;
	justify-content: center;
}

#addSong2{
	display:flex;
	justify-content:center;
	padding:20px;
}

#addSongBtn{
	padding:10px;
	width:400px;
	cursor: pointer;
	color:purple;
	background-color:aquamarine;
	border: none;
	border-radius:5px;
	font-family:'Courier New', Courier, monospace;
	font-size: 30px;
}
#addSongBtn:hover{ 
	background-color: #03e9f4; 
	color: #fff; 
	border-radius: 5px; 
	box-shadow: 0 0 5px #03e9f4, 
				0 0 25px #03e9f4, 
				0 0 50px #03e9f4, 
				0 0 100px #03e9f4; 
} 
#songArtist, #songGenre, #songLink, #songName{
padding:15px;
width: 90%;	
margin:10px;
border-radius:10px;
border: none;
}

	