 nav {
   display: flex;
   flex-direction: row;
   flex-wrap: wrap;
   justify-content: space-between;
   height: auto;
 }

 .l1 {
   text-decoration: none;
   color: white;
   margin: 10px;
   padding-top: 25px;
   font-family: Verdana, Geneva, Tahoma, sans-serif;
 }

 .l1:hover {
   color: rgb(255, 230, 0);
   opacity: 1;
   transform: scale(1.1);
 }

 .active {
   text-decoration: none;
   background-color: #dbd648;
   border: none;
   border-radius: 20px;
   width: 130px;
   height: 35px;
   text-align: center;
   margin-top: 30px;
   color: purple;
   font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
   font-size: 25px;
 }

 #icons {
   color: #1078da;
 }


 /*
https://images.unsplash.com/photo-1614680376573-df3480f0c6ff?ixlib=rb-4.0.3&q=85&fm=jpg&crop=entropy&cs=srgb&dl=alexander-shatov-qt7S2QuAhJI-unsplash.jpg
 */
 nav,
 .container {
   background-image: url('../Pages/MusiconImg2.jpeg');
   background-repeat: no-repeat;
   background-attachment: fixed;
   background-position: center;
   background-size: cover;
   height: auto;
 }

/*
https://images.unsplash.com/photo-1614680376573-df3480f0c6ff?ixlib=rb-4.0.3&q=85&fm=jpg&crop=entropy&cs=srgb&dl=alexander-shatov-qt7S2QuAhJI-unsplash.jpg
*/

 #formDiv,
 #dispSongsDiv1 {
   background-image: url('../Pages/MusiconImg2.jpeg');
   background-repeat: no-repeat;
   background-attachment: fixed;
   background-position: center;
   background-size: cover;
   width: 100%;
   height: auto;
 }



 #dispSongsDiv {
   background-image: url('../Pages/img2.jpg');
   background-repeat: repeat;
   background-position: center;
   background-size: cover;
   width: 100%;
   height: auto;
 }

 .pro-image:hover img {
   transform: scale(1.1);
 }

 audio::-webkit-media-controls-panel {
   background-color: #56AEFF;
 }

 audio::-webkit-media-controls-play-button {
   background-color: #B1D4E0;
   height: 30px;
   width: 30px;
   border-radius: 50%;
 }

 audio::-webkit-media-controls-play-button:hover {
   background-color: rgba(177, 212, 224, .7);
 }

 audio::-webkit-media-controls-timeline {
   background-color: #B1D4E0;
   border-radius: 25px;
   margin-left: 10px;
   margin-right: 10px;
 }

 