h1{
	text-align: center;
	color:yellow;
	font-family: monospace;
}
.Pwd, .Email{
	color:aquamarine;
}
#login-fieldSet{
	width:60%;
	height:auto;
	font-family: sans-serif;
	padding:20px;
	border: none;
	border-radius:20px;
}

#Login-Div{
	display:flex;
	flex-direction:row;
	flex-wrap:wrap;
	justify-content: center;
	border-radius: 20px;
	border:none;
}

#div2{
	display:flex;
	justify-content:center;
	padding:20px;
}

#LoginButt:hover, .Logbtn:hover, #forgot:hover  { 
	background: #03e9f4; 
	color: #fff; 
	border-radius: 5px; 
	box-shadow: 0 0 5px #03e9f4, 
				0 0 25px #03e9f4, 
				0 0 50px #03e9f4, 
				0 0 100px #03e9f4; 
} 

#LoginButt, .Logbtn, #forgot{ 
	background: #03e9f4; 
	color: #fff; 
	border-radius: 5px; 
	box-shadow: 0 0 5px #03e9f4, 
				0 0 25px #03e9f4, 
				0 0 50px #03e9f4, 
				0 0 100px #03e9f4; 
}

#em, #ps{
padding:20px;
width: 90%;	
margin:10px;
border-radius:15px;
border:none;
}

#ad{
width: 90%;
height:50px;	
margin: 10px;
}

#div3{
    display: flex;
    justify-content: center;
}


.Logbtn {
	border: none;
	background-color: orange;
	height: 25px;
    border-radius: 8px;
    cursor: pointer;
	font-family: Verdana, Geneva, Tahoma, sans-serif;
	margin-top:20px;
}

#LoginButt{
	border: none;
    background-color:yellow;
	color:purple;
	height: 50px;
	width: 400px;
	margin-top:20px;
    border-radius: 8px;
    cursor: pointer;
	font-family:'Courier New', Courier, monospace;	
	font-size: 30px;
}
#forgot{
	border: none;
	background-color: orange;
	height: 25px;
    border-radius: 8px;
    cursor: pointer;
	font-family: Verdana, Geneva, Tahoma, sans-serif;
}



#LogDiv{
    background-image: url('https://images.unsplash.com/photo-1614680376573-df3480f0c6ff?ixlib=rb-4.0.3&q=85&fm=jpg&crop=entropy&cs=srgb&dl=alexander-shatov-qt7S2QuAhJI-unsplash.jpg');
    background-repeat:no-repeat;
    background-attachment:fixed;
    background-position:center;
    background-size:cover;
    width: 100%;
    height:auto;
}
