nav{
	display:flex;
	flex-direction:row;
	flex-wrap:wrap;
	justify-content:flex-start;
    background-color: black;
	height: auto;
}

.regForm, #regNav{
    background-image: url('../Pages/Img1.jpg');
    background-repeat:no-repeat;
    background-attachment:fixed;
    background-position:center;
    background-size: cover;
    width: 100%;
    height:auto;
}
h1{
	text-align: center;
	color:yellow;
	font-family: Verdana, Geneva, Tahoma, sans-serif;
}
b{
	color:#49eb34;
}
.reg-fieldset{
	width: 50%;
	font-family: sans-serif;
    border:1px solid #dbdfe5;
    border-radius: 10px;
    padding: 30px;
}

.div1{
	display:flex;
	flex-direction:row;
	flex-wrap:wrap;
	justify-content: center;
}

.div2{
	display:flex;
	justify-content:center;
	padding: 20px;
}


.btn , .btn1, .reg-fieldset{ 
	background: #03e9f4; 
	color: #fff; 
	border-radius: 5px; 
	box-shadow: 0 0 5px #03e9f4, 
				0 0 25px #03e9f4, 
				0 0 50px #03e9f4, 
				0 0 100px #03e9f4; 
}

.btn:hover, .btn1:hover{ 
	background-color: #03e9f4; 
	color: #fff; 
	border-radius: 5px; 
	box-shadow: 0 0 5px #03e9f4, 
				0 0 25px #03e9f4, 
				0 0 50px #03e9f4, 
				0 0 100px #03e9f4; 
} 
.us, .em, .ps, textarea{
padding:15px;
width: 90%;	
margin:10px;
border-radius:10px;
border: none;
}
input[type="radio"] {	
margin: 10px;
}
.ad{
width: 90%;
height:50px;	
margin: 10px;
border:none;
}

.div3{
    display: flex;
    justify-content: center;
}

.btn1{
    border: none;
	background-color: orange;
	height: 25px;
    border-radius: 8px;
    cursor: pointer;
	font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.btn{
    border: none;
	background-color:yellow;
	height:50px;
	width: 300px;
	text-align: center;
    border-radius:8px;
    cursor: pointer;
	font-family:'Courier New', Courier, monospace;
	font-size:30px;
	color:purple;
}

.logo{
    font-family: monospace;
    font-style:normal;
    font-size:28px;
    letter-spacing:.3rem;
    color:springgreen;
    text-decoration:none;
}


span{
	color:white;
}
span:hover{
	color:blueviolet;
}

